@charset "UTF-8";
:root {
  scroll-behavior: inherit;
  --bs-gutter-x: 20px;
  --fs-72-32: 4.5rem;
  --fs-72-24: var(--fs-72-32);
  --fs-48-24: 3rem;
  --fs-48-sm-18: var(--fs-48-24);
  --fs-48-xs-18: var(--fs-48-24);
  --fs-36-24: 2.25rem;
  --fs-36-sm-18: var(--fs-36-24);
  --fs-32-24: 2rem;
  --fs-32-sm-18: var(--fs-32-24);
  --fs-32-sm-16: var(--fs-32-24);
  --fs-24-18: 1.5rem;
  --fs-24-lg-16: var(--fs-24-18);
  --fs-24-sm-16: var(--fs-24-18);
  --fs-18-16: 1.125rem;
  --fs-18-14: var(--fs-18-16);
  --fs-16-14: 1rem;
  --fs-16-sm-12: var(--fs-16-14);
  --fs-16-xs-12: var(--fs-16-14);
  --fs-16-sm-10: var(--fs-16-14);
  --fs-16-xs-10: var(--fs-16-14);
  --fs-14-12: 0.875rem;
  --fs-14-sm-10: var(--fs-14-12);
  --fs-14-xs-10: var(--fs-14-12);
  --size-60-50: 60px;
  --size-60-40: 60px;
  --size-50-40: 50px;
  --size-50-30: 50px;
  --size-40-30: 40px;
  --size-30-25: 30px;
  --border-raduis-24: 24px;
  --border-raduis-18: 18px;
  --border-raduis-16: 16px;
  --border-raduis-main: 2px;
  --page-gap: 60px;
  --header-height: 64px;
  --main-color: #3271D2;
  --main-color-hover: #3271D2;
  --accent-color: #132E57;
  --accent-color-hover: #132E57;
  --second-color: #727477;
  --second-light-color: #DDDEE0;
  --text-color: #57595D;
  --body-bg-color: #fff;
  --shadow: 8px 8px 15px 0px rgba(0,0,0,.1);
}

@media (max-width: 1699px) and (min-width: 0px) {
  :root {
    --fs-72-32: 3rem;
    --fs-48-24: 2.25rem;
    --fs-36-24: 2rem;
    --fs-32-24: 1.875rem;
  }
}
@media (max-width: 1399px) and (min-width: 0px) {
  :root {
    --fs-72-32: 2.625rem;
    --fs-36-24: 1.5rem;
    --fs-32-24: 1.5rem;
    --fs-24-18: 1.375rem;
    --fs-16-14: 0.875rem;
    --fs-14-12: 0.75rem;
  }
}
@media (max-width: 1199px) and (min-width: 0px) {
  :root {
    --fs-72-32: 2.25rem;
    --fs-48-24: 2rem;
    --fs-24-18: 1.125rem;
    --fs-24-lg-16: 1rem;
    --fs-18-16: 1rem;
    --fs-18-14: 0.875rem;
    --size-60-50: 50px;
    --size-60-40: 40px;
    --size-50-40: 40px;
    --size-50-30: 30px;
    --size-40-30: 30px;
    --size-30-25: 25px;
    --wrapper-gap: 40px;
  }
}
@media (max-width: 991px) and (min-width: 0px) {
  :root {
    --fs-72-32: 2rem;
    --fs-48-24: 1.5rem;
  }
}
@media (max-width: 767px) and (min-width: 0px) {
  :root {
    --fs-72-24: 1.5rem;
    --fs-48-sm-18: 1.125rem;
    --fs-36-sm-18: 1.125rem;
    --fs-32-sm-18: 1.125rem;
    --fs-32-sm-16: 1rem;
    --fs-24-sm-16: 1rem;
    --fs-16-sm-12: 0.75rem;
    --fs-16-sm-10: 0.625rem;
    --fs-14-sm-10: 0.625rem;
  }
}
@media (max-width: 576px) and (min-width: 0px) {
  :root {
    --bs-gutter-x: 15px;
    --fs-48-xs-18: 1.125rem;
    --fs-16-xs-12: 0.75rem;
    --fs-16-xs-10: 0.625rem;
    --fs-14-xs-10: 0.625rem;
  }
}
@keyframes modalAnimation {
  0% {
    opacity: 0;
    display: none;
  }
  100% {
    opacity: 1;
    display: block;
  }
}
@keyframes modalAnimationClose {
  0% {
    opacity: 1;
    display: block;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@keyframes up {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2px);
  }
}
@keyframes down {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(2px);
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
div,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
dfn,
del,
p,
blockquote,
fieldset,
input,
textarea,
select,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
button,
canvas,
video {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  color: inherit;
}

a,
a:hover,
a:focus {
  text-decoration: none;
  outline: 0;
}

button,
button:hover,
button:focus,
.btn:focus,
.btn:active:focus {
  outline: 0;
}

button {
  border: none;
  background: none;
  margin: 0;
  padding: 0;
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: auto;
}

textarea {
  max-width: 100%;
  width: 100%;
  min-width: 100%;
  min-height: 40px;
}

ul,
ol,
dl {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
p,
dt,
dd {
  padding: 0;
  margin: 0;
}

b,
dt,
h1,
h2,
h3,
h4,
h5,
strong {
  font-family: "Montserrat-Bold", sans-serif;
  font-weight: normal;
}

s {
  text-decoration-color: var(--main-color);
}

html {
  height: 100%;
  font-size: 16px;
}

body {
  height: 100%;
  min-width: 320px;
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: var(--text-color);
  line-height: 120%;
  background: var(--body-bg-color);
}

header {
  flex: 0 0 auto;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

/* APP */
.wrapper {
  position: relative;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

@media (max-width: 1699px) and (min-width: 0px) {
  .breakpoints {
    font-size: 13px;
  }
}
@media (max-width: 1399px) and (min-width: 0px) {
  .breakpoints {
    font-size: 12px;
  }
}
@media (max-width: 1199px) and (min-width: 0px) {
  .breakpoints {
    display: none;
  }
}

/* SHARED */
.text-preview {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}
.text-preview-open {
  cursor: pointer;
}
.text-preview.open {
  line-clamp: inherit;
}
.text-line-1 {
  line-clamp: 1;
}
.text-line-2 {
  line-clamp: 2;
}
.text-line-3 {
  line-clamp: 3;
}
.text--is-clamped {
  cursor: help;
}

.icon {
  stroke: currentColor;
}
.icon-fill {
  fill: currentColor;
}
.icon-rotate-90 {
  transform: rotate(90deg);
}
.icon-rotate-180 {
  transform: rotate(180deg);
}
.icon-rotate-270 {
  transform: rotate(270deg);
}

/* ENTITIES */
.nav {
  display: flex;
  align-items: center;
  gap: 32px;
  color: #fff;
}
.nav .break-nav {
  display: block;
  width: 1px;
  height: 100%;
  background-color: #fff;
}

/* FEATURES */
.form input {
  height: var(--size-60-50) !important;
  padding-left: 30px !important;
  border: none;
  outline: none;
}
.form .form-floating label {
  padding-left: 30px !important;
  font-size: var(--fs-16-14);
}
@media screen and (max-width: 1199px) {
  .form .form-floating label {
    padding: 0.85rem 0.75rem;
  }
}
.form button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: var(--size-60-50);
  padding: 0 30px;
}

.search-form {
  position: relative;
  width: 100%;
}
.search-form input {
  display: -webkit-box;
  line-clamp: 1;
  box-orient: vertical;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  height: 40px;
  padding: 0 16px 0 48px;
  font-size: var(--fs-16-14);
  line-height: 120%;
  color: #fff;
  background: transparent;
  border-radius: 50px;
  border: none;
  outline: none;
  box-shadow: 0 0 0 0.07rem #fff;
  transition: all 0.2s ease-out;
}
.search-form input::placeholder {
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  font-size: var(--fs-16-14);
  line-height: 120%;
}
.search-form input:hover, .search-form input:focus {
  box-shadow: 0 0 0 0.07rem var(--accent-color-hover);
}
.search-form button {
  position: absolute;
  top: 50%;
  left: 16px;
  color: #fff;
  border: none;
  background: none;
  transform: translateY(-60%);
  transition: all 0.2s ease-out;
}
.search-form button:hover {
  color: var(--accent-color-hover);
}

/* WIGETS */
.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: var(--header-height);
  padding: 12px 0;
  background: #fff;
  z-index: 10;
  transition: all 0.2s ease-out;
  background: var(--main-color);
}
.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__search {
  width: 100%;
  max-width: 390px;
}

.footer {
  margin-top: var(--page-gap);
  padding: 20px 0;
  background: #fff;
}

/* PAGES */
.page {
  padding-top: calc(var(--header-height) + var(--page-gap));
}
.page__title {
  font-size: var(--fs-48-24);
  font-family: "Montserrat-Bold", sans-serif;
}
.page__subtitle {
  font-size: var(--fs-24-18);
  font-family: "Montserrat-Bold", sans-serif;
}
.page__heading {
  font-size: var(--fs-18-16);
  font-family: "Montserrat-Bold", sans-serif;
}
.page__checkbox {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.page__checkbox:hover svg rect {
  fill: var(--main-color-hover);
}
.page__checkbox:hover svg polyline {
  stroke: var(--main-color-hover);
}
.page__checkbox input {
  position: absolute;
  left: 10px;
  bottom: 0;
  width: 1px !important;
  height: 1px !important;
  border: none !important;
  box-shadow: none !important;
}
.page__checkbox input:checked ~ svg rect {
  fill: var(--main-color);
}
.page__checkbox input:checked ~ svg polyline {
  stroke: #fff;
}
.page__checkbox svg {
  margin-right: 10px;
  flex-shrink: 0;
}
.page__checkbox svg rect {
  fill: var(--second-light-color);
}
.page__checkbox svg polyline {
  stroke: var(--second-light-color);
}
.page__radio {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.page__radio:hover svg rect:last-child {
  fill: var(--main-color);
}
.page__radio input {
  width: 1px;
}
.page__radio input:checked ~ svg rect:first-child {
  fill: var(--main-color);
}
.page__radio svg {
  margin-right: 10px;
}
.page__radio svg rect {
  fill: var(--second-light-color);
}
.page__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.page__content img {
  width: 100%;
  max-height: 600px;
  border-radius: 10px;
}
.page__content h1 {
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: var(--fs-32-24);
  color: #ccc;
}
.page__content h2 {
  font-size: var(--fs-24-lg-16);
  font-family: "Montserrat-Bold", sans-serif;
}
.page__content h3 {
  font-size: var(--fs-18-14);
  font-family: "Montserrat-Bold", sans-serif;
}
.page__content a {
  display: block;
  width: fit-content;
}
.page__content q {
  font-size: var(--fs-18-16);
}
.page__content p {
  font-size: var(--fs-16-14);
}
.page__content span {
  font-size: 12px;
  color: var(--accent-color);
}
.page__content ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding-left: 15px;
  list-style: none;
}
.page__content ul li {
  padding-left: 15px;
}
.page__content ul li::marker {
  content: "—";
  color: var(--main-color);
}
.page__content ol {
  display: flex;
  flex-direction: column;
  gap: 15px;
  list-style-type: none;
  counter-reset: num;
}
.page__content ol li {
  counter-increment: num;
  display: flex;
  gap: 10px;
}
.page__content ol li:before {
  content: counter(num);
  color: var(--main-color);
}
.page__content ol li:nth-child(-n+9)::before {
  content: "0" counter(num);
}
.page__content dl div {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 15px 0;
  font-size: var(--fs-14-12);
  border-bottom: 1px solid var(--second-color);
}
.page__content dl div dt {
  font-family: "Montserrat-Regular", sans-serif;
}
.page__content dl div dd {
  font-family: "Montserrat-Bold", sans-serif;
}
.page__content table {
  display: block;
  overflow-x: auto;
}
.page__content table thead tr {
  color: #fff;
  font-family: "Montserrat-Bold", sans-serif;
  background: #000;
}
.page__content table tbody tr:nth-child(even) {
  background: var(--second-light-color);
}
.page__content table tbody tr:hover {
  background: var(--accent-color);
  color: #fff;
}
.page__content table td {
  padding: 20px;
  font-size: var(--fs-16-sm-12);
  font-weight: normal;
}
.page__content table td:first-child {
  border-radius: 16px 0 0 16px;
}
.page__content table td:last-child {
  border-radius: 0 16px 16px 0;
}

@media (max-width: 1199px) and (min-width: 0px) {
  .page__content .cols-2-1 {
    gap: 40px;
  }
  .page__content table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
    width: 100%;
  }
  .open-menu {
    padding-right: 0px;
  }
  .bottom-fixed {
    display: flex;
  }
  .offcanvas__close {
    right: -30px;
  }
}